*{
    margin: 0;
    padding: 0;
 }
 .contact{
    height: 100vh;
    width: 100%;
    background-position: center;
    background-size: cover;
    box-sizing: border-box;
    position: relative;
    margin-bottom: -200px;
 }
 .navbar {
    width: 100%;
    height: 15vh;
    margin: auto;
    display: flex;
    align-items: center;
 
 }
 .topnav-right {
   float: right;
   font-size: 20px;
   color: white;
 }
 .nime {
   font-size: 22px;
   cursor: pointer;
   color: orange
 }
 .logo{
    font-size: 22px;
 }
 nav {
    flex: 1;
    padding-left: 60px;
 }
 nav ul li {
    display: inline-block;
    list-style: none;
    margin: 0px 20px;
 }
 nav ul li a {
    text-decoration: none;
    color: black;
 }
 nav ul li a:hover{
    background-color: #b0c2c3;
    height: 40px;
    width: 100px;
    padding: .50rem !important;
    align-items: center;
 }
 .spy-family-img {
    height: 90%;
    position: absolute;
    bottom: 0;
    left: 130px;
 }
 .content p{
    left: 700px;
    top: 200px;
    position: absolute;
    font-size: 2rem;
 }
 .content button{
    background-color: white;
    border: none;
    height: 40px;
    width: 150px;
    border-radius: 20px;
    left: 700px;
    top: 330px;
    position: absolute;
    cursor: pointer;
 }
 .content button:hover{
    box-shadow: 0 8px 8px gray;
 }
