 }
 .social-links {
    transform: rotate(-90deg);
    position: absolute;
    left: -80px;
    bottom: 180px;
 }
 .social-links a {
    text-decoration: none;
    color: white;
    padding-right: 20px;
    font-size: 14px;
 }
 
 
 .fade-in {
    opacity: 0;
    animation: fadeIn 1s forwards;
 }
 
 @keyframes fadeIn {
    to {
        opacity: 2;
    }
 }
 
form {
    background-color: transparent;
    border: 1px solid rgba(255, 255, 255, 0.8);
    box-shadow: 0 8px 8px rgba(0, 0, 0, 0.5);
    width: 40%;
    height: 320px;
    border-radius: 20px;
    padding: 30px;
    flex-direction: column;
    box-sizing: border-box;
    display: flex;
    gap: 20px;
    left: 650px;
    top: 200px;
    position: absolute;
 }
 form h2 {
    margin-left: 160px;
    font-size: 20px;
    color: white;
    margin-top: -15px;
    
 }
 input, textarea {
    border: none;
    border-bottom: 2px solid white;
    padding: 5px;
    background: transparent;
    transition: transform 0.3s ease-in-out, border-bottom 0.3s ease-in-out;
    color: white;
 }
 input:focus, textarea:focus {
    outline: none;
    border-bottom: 2px solid #007BFF;
    transform: translateY(10px);
 }
 label {
    font-weight: bold;
    font-size: 18px;
    color: white;
    margin-left: -5px;
    margin-top: -20px;
