
*{
   margin: 0;
   padding: 0;
}
.container {
   height: 90vh;
   width: 100%;
   background-position: center;
   background-size: cover;
   box-sizing: border-box;
   position: relative;
   margin-bottom: -180px;
}
.navbar {
   width: 100%;
   height: 15vh;
   margin: auto;
   display: flex;
   align-items: center;

}
.topnav-right {
  float: right;
  font-size: 20px;
  color: white;
}
.nime {
  font-size: 22px;
  cursor: pointer;
  color: orange
}
.streaming{
   font-family: 'Courier New', Courier, monospace !important;
   
}
.logo{
   font-size: 22px;
}
nav {
   flex: 1;
   padding-left: 60px;
}
nav ul li {
   display: inline-block;
   list-style: none;
   margin: 0px 20px;
}
nav ul li a {
   text-decoration: none;
   color: black;
}
nav ul li a:hover{
   background-color: #b0c2c3;
   height: 40px;
   width: 100px;
   padding: .50rem !important;
   align-items: center;
}
nav ul li a.active { 
   background-color: #b0c2c3; /* Background color for hover and active states */
   color: white; /* Text color for hover and active states */
   height: 40px;
   width: 100px;
   padding: 1rem !important;
   align-items: center;
   transition: all 0.3s ease; /* Smooth transition */
}

.spy-family-img {
   height: 90%;
   position: absolute;
   top: 90px;
   left: 130px;
}
.content p{
   left: 700px;
   top: 200px;
   position: absolute;
   font-size: 2rem;
}
.content button{
   background-color: white;
   border: none;
   height: 40px;
   width: 150px;
   border-radius: 20px;
   left: 700px;
   top: 330px;
   position: absolute;
   cursor: pointer;
}
.content button:hover{
   box-shadow: 0 8px 8px gray;
}
.spy-font{
   width: 200px;
   left: 770px;
   top: -380px;
   position: relative;
}
.spy-font2{
   width: 310px;
   height: auto;
   left: 500px;
   position: relative;
   top: -10px;
}
.img {
  width: 100;
  height: 100;
  margin-bottom: 20;
}
.social-links {
   transform: rotate(-90deg);
   position: absolute;
   left: -80px;
   bottom: 180px;
}
.social-links a {
   text-decoration: none;
   color: white;
   padding-right: 20px;
   font-size: 14px;
}


.fade-in {
   opacity: 0;
   animation: fadeIn 1s forwards;
}

@keyframes fadeIn {
   to {
       opacity: 2;
   }
}


@keyframes slideInRight {
   0% {
     transform: translateX(100%); /* Start off-screen to the right */
     opacity: 0; /* Start invisible */
   }
   100% {
     transform: translateX(0); /* End at original position */
     opacity: 1; /* End fully visible */
   }
 }