*{
    margin: 0;
    padding: 0;
 }
 .about{
    height: 650px;
    width: 100%; 
    background-position: center;
    background-size: cover;
    box-sizing: border-box;
    position: relative;
    margin-bottom: -600px;
    padding-top: -10px !important;
}
 .navbar {
    width: 100%;
    height: 15vh;
    margin: auto;
    display: flex;
    align-items: center;
    background-color: #395f60;

 
 }
 .topnav-right {
   float: right;
   font-size: 20px;
   color: white;
 }
 .nime {
   font-size: 22px;
   cursor: pointer;
   color: orange;
   margin-left: -1px;
 }
 .logo{
    font-size: 22px;
    margin-left: 200px;
 }
 nav {
    flex: 1;
    padding-left: 60px;
 }
 nav ul li {
    display: inline-block;
    list-style: none;
    margin: 0px 20px;
 }
 nav ul li a {
    text-decoration: none;
    color: white !important;
    padding: .50rem;
 }
 nav ul li a:hover {
   background-color: #b0c2c3;
   color: white;
   height: 40px;
   width: 100px;
   padding: .50rem !important;
   align-items: center;
}

 .spy-family-img {
    height: 90%;
    position: absolute;
    bottom: 0;
    left: 130px;
 }
 .content p{
    left: 700px;
    top: 200px;
    position: absolute;
    font-size: 2rem;
 }
 .content button{
    background-color: white;
    border: none;
    height: 40px;
    width: 150px;
    border-radius: 20px;
    left: 700px;
    top: 330px;
    position: absolute;
    cursor: pointer;
 }
 .content button:hover{
    box-shadow: 0 8px 8px gray;
 }
 .spy-font{
    width: 200px;
    left: 950px !important;
    top: -420px !important;
    position: absolute;
    animation: slideInRight 1s ease-in-out forwards;
 }
 .spy-font2{
   width: 310px;
   height: auto;
   left: 650px !important;
   position: absolute;
   top: -30px !important;
   animation: slideInRight 1s ease-in-out forwards;
}
 .watchnow{
   margin-left: -380px;
   margin-top: -150px;
 }
 .img {
   width: 100;
   height: 100;
   margin-bottom: 20;
 }
 .social-links {
    transform: rotate(-90deg);
    position: absolute;
    left: -80px;
    bottom: 180px;
 }
 .social-links a {
    text-decoration: none;
    color: white;
    padding-right: 20px;
    font-size: 14px;
 }



 .box{
    width: 500px;
    height: 250px;
    left: 270px;
    top: 240px;
    position: absolute;
    border: 1px solid rgba(255, 255, 255, 0.8);
    box-shadow: 0 8px 10px rgba(0, 0, 0, 0.5);
    border-radius: 20px;
    padding: 30px;
    flex-direction: column;
    box-sizing: border-box;
    transition: transform 1s ease;
 }

 .font-red{
    height: 80px;
    width: 120px;
    top: -5px;
    position: absolute;
 }
 .box h3{
    left: 150px;
    position: relative;
    color: white;
 }
 .box h4{
    color: white;
    top: 40px;
    position: relative;
 }
 .box button{
    top: 180px;
    left: 20px;
    height: 30px;
    width: 120px;
    cursor: pointer;
 }
 .box button:hover{
    box-shadow: 0 8px 8px gray;
 }


@keyframes slideInRight {
   0% {
     transform: translateX(100%); /* Start off-screen to the right */
     opacity: 0; /* Start invisible */
   }
   100% {
     transform: translateX(0); /* End at original position */
     opacity: 1; /* End fully visible */
   }
 }
 
 